import React from "react";
import { connect } from "react-redux";
import { toast } from 'react-toastify';
import moment from "moment";

class ReBond extends React.Component {

    constructor(props) {
        super(props);

        setInterval(() => {
            props.dispatch({ type: "GET_FIRE_VALUE" });
            props.dispatch({ type: "GET_REVERSE_BOND_USER_INFO" });
            props.dispatch({ type: "GET_GLOBAL_RE_BOND_INFO" });
        }, 1000);
        this.state = {
            amount: 1
        };
        this.handleInput = this.handleInput.bind(this);
        this.reBondFire = this.reBondFire.bind(this);
    }


    static getDerivedStateFromProps(props, state) {
        var ret = {
            coins: props.bondCurrencyList,
        }
        return ret;
    }

    handleInput(event) {
        this.setState({ amount: event.target.value });
    }

    reBondFire() {
        if (!this.props.can_perform) {
            toast.info("Please wait. Another transaction is running.", {
                position: "top-center",
                autoClose: 3000,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
            return;
        }

        if (Number(this.state.amount) < 1) {
            toast.info("Smaller than minimum bond amount.", {
                position: "top-center",
                autoClose: 3000,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
            return;
        }

        this.props.dispatch({ type: "UPDATE_CAN_PERFORM_STATUS", payload: { can_perform: false } });
        this.props.dispatch({ type: "REVERSE_BOND_FIRE", payload: { amount: this.state.amount } });
    }

    redeemFire() {
        if (!this.props.can_perform) {
            toast.info("Please wait. Another transaction is running.", {
                position: "top-center",
                autoClose: 3000,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
            return;
        }
        if (this.props.ReBondCurrentTime - this.props.ReBondCreateTime < this.props.ReBondInterval * 3600) {
            toast.error("You should only redeem once per 8 hours.", {
                position: "top-center",
                autoClose: 3000,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
            return;
        }

        this.props.dispatch({ type: "UPDATE_CAN_PERFORM_STATUS", payload: { can_perform: false } });
        this.props.dispatch({ type: "REDEEM_REVERSE_BOND" });
    }

    render() {
        return (
            <>
                <section id="section-bond">
                    <div className="content mx-auto">
                        <div className="msg-left bondLeft">
                            <div className="msg-left-content-container">
                                <div className="msg-content-item" style={{ justifyContent: "center" }}>
                                    <div className="fs-22 c-w myBonded">My Bonded Fire</div>
                                    <div className="fs-22 c-w"><span className="c-yellow noto-bold">{this.props.bondAmount ? Number(this.props.bondAmount).toFixed(2) : 0}</span></div>
                                </div>
                                <div className="msg-content-item mainStats">
                                    <div className="fs-22 c-w">Daily Redeem</div>
                                    <div className="fs-22 c-w"><span className="c-yellow noto-bold">${this.props.ReBondPrice ? this.props.ReBondPrice : '---'} </span></div>
                                    <div className="fs-18 c-w dashbSmall">Per 1 FIRE</div>
                                </div>
                            </div>
                            <div className="msg-left-content-container">
                                <div className="msg-content-item mainStats" style={{ padding: "10px 20px" }}>
                                    <div className="fs-22 c-w">Last Redeem</div>
                                    <div className="fs-20 c-yellow noto-bold">
                                        {this.props.ReBondCreateTime && this.props.ReBondCreateTime != "0" ? moment(this.props.ReBondCreateTime * 1000).format("YYYY-MM-DD H:mm:ss") : ""}
                                    </div>

                                </div>
                                <div className="msg-content-item mainBuy" style={{ justifyContent: 'center' }}>
                                    <div className="fs-18 c-gray dashbSmall">
                                        <button className="btn action-btn" style={{ width: "100%" }} onClick={() => { this.redeemFire() }}>REDEEM</button>
                                    </div>
                                </div>
                            </div>
                            <div className="msg-left-content-container">
                                <div className="msg-content-item" style={{ justifyContent: "center" }}>
                                    <div className="fs-22 c-w">Treasury Balance</div>
                                    <div className="fs-22 c-yellow noto-bold">${this.props.treasury_balance ? String(this.props.treasury_balance).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,') : 0}</div>
                                </div>
                                <div className="msg-content-item" style={{ justifyContent: "center" }}>
                                    <div className="fs-22 c-w">Total Bonds</div>
                                    <div className="fs-22 c-yellow noto-bold">${this.props.ReBondTotal ? Number(this.props.ReBondTotal).toFixed(3) : 0}</div>
                                </div>
                            </div>
                        </div>
                        <div className="msg-right">
                            <div className="token_chart bond_panel flex flex-col" style={{ padding: "10px 20px" }} >
                                    
                                <div className="flex w-full h-40 bondTitle">
                                    <div className="c-w fs-20" style={{ flex: "3" }}>Bond FIRE for USDC.<span className="smallCap">e</span></div>
                                </div>

                                <div className="w-full h-full flex align-center">
                                    <div className="flex w-full h-40">
                                        
                                        <div className="c-w fs-20" style={{ flex: "2" }}>
                                            <input className="form-control bond-input" value={this.state.amount} onChange={(event) => { this.handleInput(event) }} />
                                        </div>
                                        <div className="c-w fs-20" style={{ flex: "2" }}>
                                            <div className="pay-button list" style={{ width: "150px" }} onClick={() => { this.reBondFire(); }}>Bond</div>
                                        </div>
                                    
                                    </div>
                                </div>

                                <div className="w-full h-full flex align-center infoHold">
                                         <div className="c-w fs-20 fiveDay" style={{ flex: "2" }}>
                                         Term: 5 days
                                        </div>
                                        <div className="c-w fs-20 eightHr" style={{ flex: "2" }}>
                                        Interval: 8 hours
                                        </div>
                                </div>

                                <div className="w-full h-full flex align-center infoHold">
                                         <div className="c-w fs-20 fiveDay" style={{ flex: "2" }}>
                                         Bond: <span>$Nan</span>
                                        </div>
                                        <div className="c-w fs-20 eightHr" style={{ flex: "2" }}>
                                        Fire: <span>${Number(this.props.fire_value).toFixed(2)}</span>
                                        </div>
                                </div>

                            </div>
                            <div className="chart-text">
                                <p className="c-w">
                                    Text coming soon...
                                </p>
                            </div>
                        </div>
                    </div>
                </section>
            </>
        );
    }
};

const mapDispatchToProps = dispatch => {
    return { dispatch };
}

const mapStateToProps = state => {
    return {
        account: state.account,
        can_perform: state.can_perform,
        treasury_balance: state.treasury_balance,
        fire_value: state.fire_value,

        ReBondLastTime: state.reBondLastTime,
        ReBondCurrentTime: state.reBondCurrentTime,
        ReBondCreateTime: state.reBondCreateTime,
        bondAmount: state.reBondAmount,
        ReBondTotal: state.re_bond_total,
        ReBondCurrencyList: state.re_bond_currency_list,
        ReBondPrice: state.re_bond_price,
        ReBondInterval: state.re_bond_interval,
    };
}
export default connect(mapStateToProps, mapDispatchToProps)(ReBond);
